*{ 
margin: 0;

padding: 0;

} 
body{
  background-color: darkgoldenrod;
}

.container {
background: linear-gradient(to right bottom,#e1b382 #c89666);
width: 100%;
min-height: 100vh;
display: flex;
justify-content: center;
align-items: center;


}
.app-wrapper{
  background-color: #12343b;
min-width: 458px;
min-height: 650px;
padding: 30px;
box-sizing: border-box;
border-radius: 9px;
box-shadow: 2px 2px 10px #000;
margin-bottom: 0px;
}
.header h1{
  color: #fff;
  text-align: center;
  margin: 30px 0;
}
.task-input{
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #ccc;
  background-color: #000000;
  border: 1px solid #c89666;
  border-radius: 10px;
}
.button-add{
  width: 80px;
  padding: 15px 15px;
  font-size: 19px;
  border-radius: 10px;
  border: 0px;
  background-color: #f1af71;
  cursor: pointer;
  

}
.list-item{
display: flex;
margin: 20px 0;
border: 1px solid #ccc;
border-radius: 10px;
padding: 10px;
max-height: 30px
}
.button-complete,
.button-delete,
.button-edit{
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 19px;
  border-radius: 50px;

}
.button-complete{
  color: #ff6c6c;
  margin-right: 10px;
}
.button-edit{
  color: #e2d029;
  margin-right: 10px;
}
.button-delete{
  color:aqua;
 
}
.button-complete{
  color: #ff6c6c;
  margin-right: 10px;

}
.complete{
text-decoration-style:double;
text-decoration-line: line-through;
text-decoration-color:red;
opacity: 0.6;
  
}
.list{
width: 220px;
background-color: #12343b;
border: none;
color: #ccc;
font-size: 20px;
padding-left: 10px;
margin-right: 15px;

}
input:focus,
button:focus{
  outline:none;
}